export function confirmClickedUnity() {
    if(new Function(`return window.confirmClicked`)()) {
        alert('in func');
        new Function(`return window.confirmClicked('123123', 0)`);
    }
    if(window.confirmClicked) {
        window.confirmClicked('123123', 0);
        alert('there is confirmClicked')
    }else {
        alert('there is no clicked');
    }
    if(window.userLogin) {
        alert('in user login')
        window.userLogin('123123', 0);
    }
}